.grid-container {
  width: 100%;
  max-width: 1200px;
}

.row:before,
.row:after {
  content: "";
  display: block;
  height: 0;
  width: 0;
  visibility: hidden;
  clear: both;
}

[class*="col_"] {
  float: left;
  min-height: 1px;
  width: 8.33%;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -o-box-sizing: border-box;
  -ms-box-sizing: border-box;
  -webkit-box-sizing: border-box;
}
.col_1 {
  width: 8.33%;
}
.col_2 {
  width: 16.66%;
}
.col_3 {
  width: 25%;
}
.col_4 {
  width: 33.33%;
}
.col_5 {
  width: 41.66%;
}
.col_6 {
  width: 50%;
}
.col_7 {
  width: 58.33%;
}
.col_8 {
  width: 66.66%;
}
.col_9 {
  width: 75%;
}
.col_10 {
  width: 83.33%;
}
.col_11 {
  width: 91.66%;
}
.col_12 {
  width: 100%;
}
