.typesContent,
.typesContent [class*="col_"],
.typesContent .react-codemirror2,
.typesContent .CodeMirror {
  height: 100%;
}

.typesContent .row_content {
  height: calc(100% - 40px);
}

.typesContent .row_input {
  height: 40px;
}

.typesContent .row_input input {
  height: 100%;
  width: 100%;
  border: none;
  outline: none;
  border-left: 1px solid #999;
}

.typesContent {
  margin: 0 auto;
}

.col_left {
  border-right: 1px solid #999;
}

.col_right {
  position: relative;
}

.col_right:hover .copy_content {
  display: block;
}

.col_left:hover .copy_content {
  display: block;
}

.copy_content {
  display: none;
  position: absolute;
  top: 10px;
  z-index: 1000;
  right: 10px;
  color: #999;
  padding: 6px 12px;
  border: 1px solid #999;
  border-radius: 4px;
  cursor: pointer;
  font-size: 12px;
}

.copy_content:hover {
  background-color: #eee;
}
